// Color
$color-primary: rgb(255, 92, 92);
$color-secondary: rgb(255, 190, 190);

// Gradient
$gradient: linear-gradient(rgb(247, 247, 247), rgb(255, 166, 166));
$gradient-wrapper: linear-gradient(225deg, darken($color: $color-primary, $amount: 5), $color-primary);

// Font
$font-size-sm: 14px;
$font-size-md: 20px;
$font-size-lg: 26px;
$font-size-xl: 32px;
$font-size-xxl: 54px;